@import (less) "color.less";

.icheckbox-container {
	margin-right: 0.195em;
}
.icheckbox-container.checked {
  .checkbox-label {
    color: @darkest_gray;
  }
}
.icheckbox-container.disabled {
  .checkbox-label {
		opacity: 0.5;
    // color: @light_gray !important;
  }
}
.icheckbox-container.checked.disabled {
  .checkbox-label {
		opacity: 0.5;
		color: @green;
    // color: @light_gray !important;
  }
}
.focusTabCheckbox {
	box-shadow: 0 0 0.05em 0.23em rgba(37,137,0,0.6) !important
}
//TODO è NECESSARIO AGGIORNARE L'URL DELLA IMMAGINE, IN ACCORDO CON LA RISOLUZIONE DEI PERCORSI PER I PACCHETTI NPM
.icheckbox_intesa {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	padding: 0;
  margin: 0 0 2px 0;
  // width: 22px;
  // height: 22px;
  width: 24px;
  height: 24px;
	background: url(/cjlib/assets/images/sprite-margin.png) no-repeat;
	border: none;
	background-position: -1px 0;
	input[type="checkbox"] {
		// margin: 0px 0px 0 0 !important;
		margin: 4px 4px 0 0 !important;
    cursor: pointer;
    // height:22px;
    // width:22px;
    height:24px;
    width:30px;
		opacity: 0;
		&:disabled {
			cursor: auto;
		}
	}
	&.yellowborder {
    background-position: -25px 0px;
	}
}
.icheckbox_intesa.checked {
	background-position: -73px 0;
	&.yellowborder {
		background-position: -97px 0;
	}
}
.icheckbox_intesa.disabled {
	opacity: 0.5;
	// background-position: -169px 0;
	cursor: default;
	// &.yellowborder {
	// 	background-position: -169px 0;
	// }
}
.icheckbox_intesa.checked.disabled {
	opacity: 0.5;
	// background-position: -145px 0;
	// &.yellowborder {
		// background-position: -197px 0;
	// }
}
.checkbox-label {
	color: @darkest_gray;
	font-size: 0.875em !important;
	font-weight: 400;
	margin: 0 0 0 10px !important;
}
.input-checkbox {
	position: absolute;
	left: -1px;
	top: -2px;
}
